.sect-block {
    --blk-marg: 60px;
    --head-marg: 30px;
    --width: calc(100% - 2 * var(--blk-marg));
    width: var(--width);
    margin: var(--blk-marg);
    margin-bottom: 80px;
    --title-font: 64px;
    --cont-width: 85%;
    --cont-marg: calc(50% - var(--cont-width) / 2);
}

@media screen and (max-width: 780px) {
    .sect-block {
        --cont-width: 100%;
        --title-font: 42px;
        --blk-marg: 10px;
        --head-marg: 0;
    }
}

.sect-block .blk-header {
    width: calc(var(--width) - var(--cont-marg) / 2 - 10px);
    height: 72px;
    flex-grow: 1;
    position: relative; 
    font-family: Impact;
    margin-bottom: var(--head-marg);
}

.sect-block .blk-header .title{
    position: absolute;  
    text-shadow: 0px 0px 3px #eeff00;
    top: 0;
    bottom: 0;
    left: 0;
    right:0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: auto;
    height: 100%;
    font-size: var(--title-font);
    color: white;
}

.sect-block .blk-header .view-all{
    position: absolute;  
    top: 0;
    bottom: 0;
    right: 0;
    display: flex;
    width: auto;
    height: 100%;
    flex-direction: column;
    justify-content: center;
}

.sect-block .content {
    position: static;
    width: var(--cont-width);
    margin-left: var(--cont-marg);
    margin-right: var(--cont-marg);
}